/*
@signature-red: #cf0a2c;
@signature-yellow: #ffc627;
*/
html,
body {
  font-family: 'Play', sans-serif;
  -webkit-font-smoothing: antialiased;
  height: 100%;
  margin: 0;
  padding: 0; }

html {
  background: none; }

body {
  background: url("/static/signature-theaters-background1-960.png") no-repeat center center;
  background-size: cover; }

#__next,
#__next > div {
  height: 100%; }

header .navbar,
.navbar-logo,
header {
  display: inline-block;
  font-family: 'Play', sans-serif; }

header .navbar-logo {
  float: left;
  margin: 2px 0 2px 20px; }

header .navbar {
  position: relative;
  top: 0;
  border: none;
  border-radius: 0;
  border-bottom: 1px solid #cf0a2c;
  display: flex;
  align-items: center; }

header .navbar ul {
  -webkit-transition: max-height 0.8s ease-in-out;
  transition: max-height 0.8s ease-in-out; }

.navbar-nav {
  margin-left: 0; }

.navbar ul {
  overflow: hidden;
  position: absolute;
  top: 56px;
  left: -10px;
  max-height: 0;
  background: rgba(0, 0, 0, 0.6);
  margin: 0;
  padding: 0; }

.navbar ul li {
  margin-left: 10px; }

.navbar ul.new-class {
  display: inline-block;
  max-height: 1000px; }

header .navbar a {
  text-decoration: none; }

header .navbar a:hover,
header .navbar a:focus {
  text-shadow: 2px 2px 10px #cf0a2c, -2px -2px 10px #cf0a2c, -2px 2px 10px #cf0a2c, 2px -2px 10px #cf0a2c; }

.navbar-collapse {
  float: left; }

.navbar-nav {
  font-size: 18px;
  text-transform: uppercase;
  letter-spacing: 5px;
  line-height: 50px;
  margin-left: 20px; }

.navbar-toggle {
  border: 0;
  background: none;
  cursor: pointer; }

.navbar-inverse .navbar-toggle:hover {
  background: none; }

.navbar-nav > li {
  display: inline-block; }

.navbar-inverse .navbar-nav > li > a {
  color: #DDD; }

header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  margin-bottom: 0;
  padding: 0;
  margin: 0;
  padding: 0; }

header::after {
  content: " ";
  background: url(/static/triangle.svg);
  position: absolute;
  width: 100px;
  right: -100px;
  top: 0;
  box-shadow: none;
  z-index: 3;
  height: 100px; }

.navbar-inverse {
  background-color: black; }

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }

section {
  width: 100%;
  min-height: 100%;
  background: #000;
  color: #FFF;
  position: relative;
  top: 100%;
  display: flex;
  align-items: center;
  transition: all 1s ease;
  padding-bottom: 80px;
  border-top: 1px solid #ffc627; }

.experience,
.contact {
  background: url(/static/blackmamba.png); }

h1, h2, h3, h4, h5, h6 {
  font-weight: 500;
  line-height: 1.1; }

h1 {
  color: #999;
  text-transform: uppercase;
  letter-spacing: 10px;
  margin: 80px 0 40px;
  font-size: 50px; }

h2 {
  color: #ffc627;
  text-shadow: 2px 2px 10px #cf0a2c, -2px -2px 10px #cf0a2c, -2px 2px 10px #cf0a2c, 2px -2px 10px #cf0a2c;
  font-size: 30px;
  margin-bottom: 10px; }

p {
  font-size: 18px;
  margin-top: 0;
  line-height: 1.5rem; }

.row {
  margin-right: -15px;
  margin-left: -15px; }

.btn-default {
  background-color: #000;
  border-color: #CCC;
  color: #CCC;
  text-shadow: 1px 1px 1px #000, -1px -1px 1px #000, -1px 1px 1px #000, 1px -1px 1px #000;
  border-width: 2px; }

.btn-default:hover,
.btn-default:active,
.btn-default:focus,
.btn-default:active:hover {
  background-color: #000;
  border-color: #CCC;
  color: #FFF;
  text-shadow: 1px 1px 1px #000, -1px -1px 1px #000, -1px 1px 1px #000, 1px -1px 1px #000;
  box-shadow: 2px 2px 10px #cf0a2c, -2px -2px 10px #cf0a2c, -2px 2px 10px #cf0a2c, 2px -2px 10px #cf0a2c;
  position: relative; }

.btn-default:active {
  top: 1px; }

.section-button {
  margin: 60px 0 0;
  padding-bottom: 10px; }

#contact .section-button {
  margin: 0; }

#contact .section-button .btn {
  width: 100%; }

.form-control {
  font-size: 20px;
  padding: 3px 12px; }

@-webkit-keyframes animate-left {
  0% {
    -webkit-transform: translate3d(-100%, 0, 0);
    opacity: 0; }
  50% {
    opacity: .3; }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    opacity: 1; } }

@-webkit-keyframes animate-right {
  0% {
    -webkit-transform: translate3d(100%, 0, 0);
    opacity: 0; }
  50% {
    opacity: .3; }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    opacity: 1; } }

@keyframes animate-left {
  0% {
    transform: translate3d(-100%, 0, 0);
    opacity: 0; }
  50% {
    opacity: .3; }
  100% {
    transform: translate3d(0, 0, 0);
    opacity: 1; } }

@keyframes animate-right {
  0% {
    transform: translate3d(100%, 0, 0);
    opacity: 0; }
  50% {
    opacity: .3; }
  100% {
    transform: translate3d(0, 0, 0);
    opacity: 1; } }

@-webkit-keyframes animate-top {
  0% {
    transform: translate3d(0, 100%, 0);
    opacity: 0; }
  50% {
    opacity: .3; }
  100% {
    transform: translate3d(0, 0, 0);
    opacity: 1; } }

@keyframes animate-top {
  0% {
    transform: translate3d(0, 100%, 0);
    opacity: 0; }
  50% {
    opacity: .3; }
  100% {
    transform: translate3d(0, 0, 0);
    opacity: 1; } }

@-webkit-keyframes animate-bottom {
  0% {
    transform: translate3d(0, -100%, 0);
    opacity: 0; }
  50% {
    opacity: .3; }
  100% {
    transform: translate3d(0, 0, 0);
    opacity: 1; } }

@keyframes animate-bottom {
  0% {
    transform: translate3d(0, -100%, 0);
    opacity: 0; }
  50% {
    opacity: .3; }
  100% {
    transform: translate3d(0, 0, 0);
    opacity: 1; } }

.container {
  overflow: hidden; }

.service-list-item .col-xs-12,
.experience-list-item .col-xs-12,
.products-list-item .col-xs-12 {
  margin-bottom: 30px;
  margin: 1rem; }

.service-list-item img,
.experience-list-item img,
.products-list-item img {
  max-width: 100%;
  position: relative;
  border: 2px solid #FFF;
  border-radius: 10px; }

.service-list-item img,
.products-list-item img {
  min-width: 100%; }

.experience-list-item .vert-wrapper {
  overflow: hidden;
  align-self: flex-start;
  justify-content: flex-start;
  flex-grow: 0; }

.animation {
  -webkit-transition: all 1s ease-in-out;
  -webkit-transform: translate3d(-100%, 0, 0);
  transition: all 1s ease-in-out;
  transform: translate3d(-100%, 0, 0);
  opacity: 0; }

.animation.right {
  max-width: 100%;
  transform: translate3d(100%, 0, 0); }

.animation.in-view {
  -webkit-animation: animate-left 0.5s ease-in both;
  animation: animate-left 0.5s ease-in both; }

.experience-list-item .animation.in-view {
  -webkit-animation: animate-top 0.5s ease-in both;
  animation: animate-top 0.5s ease-in both; }

.experience-list-item .animation.in-view.bottom {
  -webkit-animation: animate-bottom 0.5s ease-in both;
  animation: animate-bottom 0.5s ease-in both; }

.animation.right.in-view {
  -webkit-animation: animate-right 0.5s ease-in both;
  animation: animate-right 0.5s ease-in both; }

.service-list-item,
.products-list-item {
  margin-top: 30px; }

.phone {
  font-size: 48px;
  color: #CCC;
  display: inline-block; }

.phone-label {
  display: inline-block;
  margin-right: 15px; }

@media (min-width: 768px) {
  .service-list-item .col-xs-12,
  .experience-list-item .col-xs-12,
  .products-list-item .col-xs-12 {
    width: 50%; }
  .container {
    width: 750px; }
  .col-sm-4 {
    width: 33.33333333%; }
  .navbar-nav {
    margin-left: auto;
    letter-spacing: 2px;
    display: flex; }
  .navbar ul {
    overflow: auto;
    position: static;
    top: auto;
    left: auto;
    max-height: none;
    background: none;
    margin: 0 0 0 15px;
    padding: auto; }
  .navbar ul li {
    margin: auto;
    flex: 0 1 auto; }
  .navbar-nav > li > a {
    padding: 12px; }
  .service-list-item div,
  .experience-list-item div,
  .products-list-item div {
    display: flex;
    flex-direction: column;
    justify-content: center; }
  .service-list-item .col-xs-12,
  .experience-list-item .col-xs-12,
  .products-list-item .col-xs-12 {
    margin-bottom: 0; }
  .service-list-item h2,
  .experience-list-item .col-xs-12,
  .products-list-item h2 {
    margin-top: 0; }
  .vertical-align {
    display: flex;
    flex-direction: row; }
  .vertical-align.order-right > .col-sm-6:first-child {
    order: 2; }
  .vertical-align > [class^="col-"],
  .vertical-align > [class*=" col-"] {
    display: flex;
    align-items: left;
    justify-content: center; }
  .experience-list-item.vertical-align > [class^="col-"],
  .experience-list-item.vertical-align > [class*=" col-"] {
    justify-content: flex-start; }
  .vertical-align > [class^="col-"] > div,
  .vertical-align > [class*=" col-"] > div {
    /* flex: 1; */
    flex-grow: 1; }
  .experience-list-item.vertical-align > [class^="col-"]:first-child > div,
  .experience-list-item.vertical-align > [class*=" col-"]:first-child > div {
    /* flex: 1; */
    flex-grow: 0; }
  .email {
    border: 1px solid #CCC;
    border-radius: 10px;
    box-shadow: 1px 1px 5px #000;
    background: rgba(100, 100, 100, 0.3); }
  #contact h1 {
    margin: 60px 0 0; }
  #contact h2 {
    margin-top: 20px; } }

@media (min-width: 960px) {
  .container {
    width: 942px; }
  body {
    background: url("/static/signature-theaters-background1-960.png") no-repeat center center;
    background-size: cover;
    background-attachment: fixed; }
  .navbar-nav {
    letter-spacing: 5px; }
  .navbar-nav > li > a {
    padding: 15px; }
  .service-list-item img,
  .products-list-item img {
    min-width: 100%; }
  h1 {
    font-size: 72px; } }

@media (min-width: 1280px) {
  .container {
    width: 1170px; }
  body {
    background: url("/static/signature-theaters-background1-1920.png") no-repeat center center;
    background-size: cover;
    background-attachment: fixed; } }
